body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.page-tube .item {
    border-bottom: 2px solid #f2f2f2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-left: 150px;
    margin-top: 30px;
}

.page-tube .video-type {
    font-size: 19px;
    font-weight: bold;
    margin-left: 150px;
    margin-top: 15px;
    text-align: left;
}

.page-tube .video-type .music-icon{
    background: #333;
    border-radius: 50%;
    color: #fff;
    margin-right: 8px;
    padding: 7px 10px 7px 10px;
}

.play-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 30px;
    margin-right: 5px;
}

.play-item .video-item {
    border: 1px solid #aaa;
    float: left;
    height: 125px;
    width: 225px;
}

.play-item .video-info {
    float: left;
    margin-top: 12px;
    text-align: left;
    width: 225px;
}

.play-item .video-info .desc {
    font-weight: bold;
    height: 45px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.play-item .video-info .info {
    color: #808080;
    font-size: 15px;
    font-weight: 600;
    margin-top: 5px;
}

.play-item .video-info .info .view .view-count{
    margin-right: 5px;
}

.play-item .video-info .info .title .tick-icon {
    background: #333;
    border-radius: 50%;
    color: #fff;
    padding: 5px 7px 5px 7px;
}

.header {
    background-color: #333;
    display: -webkit-flex;
    display: flex;
    height: 70px;
}

.header .leftcont {
    padding-left: 155px;
    padding-top: 20px;
    text-align: left;
    width: 70%;
}

.header .leftcont .title {
    background: linear-gradient(#0000ff, #6666ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 20px;
    font-weight: 700;
}

.header .leftcont .bars-icon {
    color: #fff;
    margin-right: 23px;
}

.header .rightcont {
    padding-right: 50px;
    padding-top: 20px;
    text-align: right;
    width: 30%;
}

.header .rightcont .search-icon {
    color: #fff;
    margin-right: 30px;
}

.header .rightcont .notification-icon {
    color: #fff;
    margin-right: 18px;
}

.header .rightcont .globe-icon {
    color: #fff;
}

.header .rightcont .down-icon {
    color: #fff;
}

.footer {
    height: 110px;
    background-color: #fff;
    box-shadow: 0 10px 6px 10px #777;
    margin-top: 50px;
    color: #a6a6a6;
    font-size: 12px;
    padding-top: 20px;
}

.footer .topcont {
    width: 100%;
    height: 40px;
    border-bottom: 2px solid #e6e6e6;
    display: -webkit-flex;
    display: flex
}

.footer .topcont .leftcont {
    text-align: left;
    padding-left: 115px;
    width: 60%;
}

.footer .topcont .rightcont {
    width: 40%;
    text-align: right;
}

.footer .topcont .leftcont .title {
    color: #cccccc;
    font-family: monospace;
    font-size: 20px;
    margin-right: 13px;
}

.footer .topcont .rightcont span {
    margin-right: 22px;
}

.footer .bottomcont {
    margin-top: 10px;
    padding-left: 115px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
}

.footer .bottomcont .left {
    display: -webkit-flex;
    display: flex;
    text-align: left;
    float: left;
    width: 75%;
}

.footer .bottomcont .right span {
    float: right;
    margin-right: 15px;
}

.footer-item {
    background-color: #000;
    border: none;
    border-radius: 3px;
    color: #fff;
    height: 25px;
    margin-right: 12px;
    padding: 2px 7px 2px 7px;
    width: 90px;
}

.footer-item .leftcont {
    text-align: left;
    width: 30px;
}

.footer-item .rightcont {
    float: right;
    text-align: left;
    width: 60px;
}

.footer-item .rightcont .subtitle{
    font-size: 7px;
}

.footer-item .rightcont .title{
    font-size: 11px;
}

